.title {
  text-align: center;
  color: #fff;
  font-size: 3.2rem;
  font-weight: 700;
}

@media only screen and (min-width: 768px) {
  .title {
    font-size: 4.2rem;
  }
}

.cover {
  width: 100%;
  max-width: 600px;
  height: 0;
  border-radius: 16px;
  padding-bottom: 41.5%;
  transition-property: box-shadow;
  transition-duration: .2s;
  transition-timing-function: ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 #0000ff80;
}

.cover img, .cover iframe, .cover video {
  width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
  position: absolute;
  inset: 0;
}

.cover:hover {
  box-shadow: 0 0 30px #0000ff80;
}

.index-page {
  min-height: 100vh;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 64px;
  display: flex;
}

@media only screen and (min-width: 768px) {
  .index-page {
    justify-content: center;
  }
}

.index-page__title {
  margin-bottom: 40px;
}

@media only screen and (min-width: 768px) {
  .index-page__title {
    margin-bottom: 80px;
  }
}

.button {
  cursor: pointer;
  white-space: nowrap;
  text-transform: uppercase;
  color: #111;
  background: #ffdb00;
  border: 2px solid #ffdb00;
  border-radius: 30px;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 8px 30px;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 17px;
  text-decoration: none;
  transition-property: border, background, color;
  transition-duration: .2s;
  transition-timing-function: ease;
  display: flex;
  position: relative;
}

.button--big {
  padding: 18px 50px;
}

@media only screen and (min-width: 768px) {
  .button:hover {
    color: #111;
    background: none;
    border: 2px solid #111;
  }

  .button--white:hover {
    background: none;
    color: #fff !important;
    border: 2px solid #fff !important;
  }
}

.button:after {
  cursor: pointer;
  opacity: 0;
  white-space: nowrap;
  text-transform: uppercase;
  color: #111;
  background: #ffdb00;
  border: 2px solid #ffdb00;
  border-radius: 30px;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 17px;
  text-decoration: none;
  transition-property: border, background, color, opacity;
  transition-duration: .2s;
  transition-timing-function: ease;
  display: flex;
  position: absolute;
  inset: 0;
}

.button--sending:after {
  content: "Sending...";
  opacity: 1;
  background: #ffdb00;
}

.tabs {
  width: calc(100% + 40px);
  -ms-overflow-style: none;
  scrollbar-width: none;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
  position: relative;
  left: -20px;
  overflow: scroll;
}

.tabs::-webkit-scrollbar {
  display: none;
}

.tab {
  opacity: .3;
  color: #111;
  white-space: nowrap;
  cursor: pointer;
  border-bottom: 2px solid #fff;
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 20px;
  font-size: 2.4rem;
  font-weight: 900;
  transition-property: opacity, border-color;
  transition-duration: .2s;
  transition-timing-function: ease;
  display: block;
}

@media only screen and (min-width: 768px) {
  .tab {
    font-size: 36px;
  }

  .tab:hover {
    opacity: .5;
  }
}

.tab.active {
  opacity: 1;
  border-bottom: 2px solid #111;
}

.cards {
  width: calc(100% + 40px);
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: 40px;
  display: none;
  position: relative;
  left: -20px;
  overflow: scroll;
}

.cards::-webkit-scrollbar {
  display: none;
}

@media only screen and (min-width: 768px) {
  .cards {
    margin-bottom: 80px;
  }
}

@media only screen and (min-width: 1024px) {
  .cards {
    left: 0;
  }
}

.cards.active {
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.card__svg__wrapper {
  width: 80px;
  height: 80px;
  margin-bottom: 40px;
  transition-property: top;
  transition-duration: .2s;
  transition-timing-function: ease;
  position: relative;
  top: 0;
}

.card__wrapper:before {
  content: "";
  background: #ffdb00;
  transition-property: top;
  transition-duration: .2s;
  transition-timing-function: ease;
  position: absolute;
  inset: 375px 0 0;
}

@media only screen and (min-width: 768px) {
  .card__wrapper:before {
    top: 425px;
  }
}

.card__wrapper .card__inner .card__svg__wrapper .card__svg:first-child {
  opacity: 0;
  position: absolute;
}

@media only screen and (min-width: 768px) {
  .card__wrapper:hover .card__inner .card__svg__wrapper {
    top: -5px;
  }

  .card__wrapper:hover .card__inner .card__svg__wrapper .card__svg:first-child {
    opacity: 1;
    position: absolute;
  }

  .card__wrapper:hover:before {
    top: 0;
  }
}

.card {
  min-height: 375px;
  min-width: calc(100vw - 100px);
  max-width: 330px;
  text-align: left;
  background: #f9f9fb;
  margin-right: 20px;
  padding: 20px;
  position: relative;
}

@media only screen and (min-width: 480px) {
  .card {
    width: 330px;
    min-width: 330px;
  }
}

@media only screen and (min-width: 768px) {
  .card {
    min-height: 425px;
    padding: 60px 30px;
  }
}

.card:not(:last-of-type) {
  margin-right: 20px;
}

.card:first-of-type {
  margin-left: 20px;
}

@media only screen and (min-width: 1024px) {
  .card:first-of-type {
    margin-left: 0;
  }

  .card:last-of-type {
    margin-right: 0;
  }
}

.card__inner {
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.card__svg {
  fill: green;
  margin-bottom: 40px;
  transition-property: opacity;
  transition-duration: .2s;
  transition-timing-function: ease;
}

.card__title {
  color: #111;
  margin-bottom: 15px;
  font-size: 2.2rem;
  font-weight: 700;
}

.card__subtitle {
  color: #111;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 24px;
}

